<template>
  <div>
    <div
      class="basket"
      v-if="basketItems.length"
    >
      <h1 class="holder">{{$t('basket.basket')}}</h1>
      <div
        class="basket__item-wrapper md-layout mar-b-2"
        v-for="(item, index) in basketItems"
        :key="index"
      >
        <div class="flex">
          <div class="flex__stretch">
            <div class="flex flex_jc-sb flex_wrap flex__no-stretch">
              <div class="mar-b-1 md-layout-item md-size-35 md-medium-size-100">
                <Card
                  class="card_horizontal"
                  :no-price="true"
                  :no-border="true"
                  :productId="item.id"
                  :cardData="item"
                  :needShowArtNum="false"
                />
              </div>
              <div class="md-layout-item md-size-35 md-medium-size-50 md-small-size-100 pad-l-2 pad-t-1 pad-r-5 pad-small-l-0">
                <div class="mar-b-1 flex flex_jc-sb">
                  <span class="mar-r-1">{{ $t('product.quantity') }}</span>
                  <Quantity
                    :minQuantity="1"
                    :maxQuantity="9999"
                    :selectedValue="item.quantity"
                    v-on:quantityValue="onQuantityChange($event)"
                  />
                </div>
              </div>
              <div class="md-layout-item md-size-30 md-medium-size-40 md-small-size-100 flex pad-t-1">
                <div class="flex__stretch">
                  <div class="md-layout md-gutter mar-b-1">
                    <div class="md-layout-item">{{ $t('basket.totalPrice') }}</div>
                    <div class="md-layout-item text-right">
                      <span class="text-green">{{ item.price.totalPrice | formatPrice}} FR</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="mar-l-1">
            <div class="md-layout-item md-size-5 text-center">
              <div
                class="round-btn flex flex_align-center pointer"
                @click="onDeleteCartItem()"
              >
                <i class="delete-icon md-icon md-icon-font md-theme-default">close</i>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="checkout-button md-layout">
        <div class="md-layout-item text-center">
          <button
            @click="startCheckout()"
            class="basket__checkout-button mar-b-3"
          >{{ $t('basket.checkout') }}
          </button>
        </div>
      </div>
    </div>
    <div
      class="basket__empty-basket-content md-layout flex"
      v-if="!basketItems.length"
    >
      <div class="md-layout-item text-center">
        <p class="empty-text">Ihr Warenkorb ist leer</p>
      </div>
    </div>

    <md-dialog :md-active.sync="isModalVisible">
      <AuthorizationModal
        v-on:changeModalState="closeModalFlag()"
        :login-type="'checkout'"
      />
    </md-dialog>
  </div>
</template>

<script>
import axios from 'axios';

import { mapActions, mapGetters } from 'vuex';
import Card from '../components/Card.vue';
import Quantity from '../components/Quantity.vue';
import AuthorizationModal from '../components/AuthorizationModal.vue';

// import common from '../assets/common';
import authRequestMixin from '../mixins/authRequestMixin';

export default {
  name: 'Basket',
  mixins: [authRequestMixin],
  components: {
    Card,
    Quantity,
    AuthorizationModal,
  },
  data: () => ({
    generalData: {},
    basketItems: [],
    currency: {},
    isModalVisible: false,
  }),
  created() {
    this.getProductsInBasket();
  },
  computed: {
    ...mapGetters(['getAllCartItems']),
  },
  methods: {
    ...mapActions([
      'updateCartItems',
    ]),
    getProductsInBasket() {
      const cartToken = localStorage.getItem('contextToken');

      if (cartToken) {
        axios
          .get('http://77.245.20.254/store-api/v3/checkout/cart',
            {
              headers: {
                'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
                'sw-context-token': cartToken,
              },
            })
          .then((response) => {
            this.basketItems = response.data.lineItems;
          })
          .catch((e) => {
            console.error('Error occured on get all basket items ', e);
            this.$toaster.error(this.$t('notify.oops'));
          });
      }
    },
    onDeleteCartItem() {
      axios
        .delete('http://77.245.20.254/store-api/v3/checkout/cart/line-item',
          {
            headers: {
              'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
              'sw-context-token': localStorage.getItem('contextToken'),
            },
            data: {
              ids: [this.basketItems[0].id],
            },
          })
        .then((response) => {
          if (response) {
            this.basketItems = response.data.lineItems;
            this.updateCartItems(response.data.lineItems);
          }
        })
        .catch((e) => {
          console.error('Error occured on delete the basket item ', e);
          this.$toaster.error(this.$t('notify.oops'));
        });
    },
    startCheckout() {
      if (!JSON.parse(localStorage.getItem('isUserAuthorized'))) {
        this.openModalFlag();
      } else {
        this.$router.push('/checkout');
      }
    },
    onQuantityChange(quantityData) {
      const dataStructure = {
        quantity: quantityData,
        type: 'product',
        referencedId: this.basketItems[0].referencedId,
        id: this.basketItems[0].id,
      };

      axios
        .patch('http://77.245.20.254/store-api/v3/checkout/cart/line-item',
          {
            items: [
              { ...dataStructure },
            ],
          },
          {
            headers: {
              'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
              'sw-context-token': localStorage.getItem('contextToken'),
            },
          })
        .then((response) => {
          this.basketItems = response.data.lineItems;
          this.updateCartItems(response.data.lineItems);
        })
        .catch((e) => {
          console.error('Error occured on change quantity in basket ', e);
          this.$toaster.error(this.$t('notify.oops'));
        });
    },
    openModalFlag() {
      this.isModalVisible = true;
    },
    closeModalFlag() {
      this.isModalVisible = false;
    },
  },
  filters: {
    formatPrice: (value => value.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')),
  },
};
</script>
